<template>
  <div class="userNav">
    <div class="navOuter">
      <div class="container">
       <div class="nacInner">
         <div class="leftImg" v-if="false">
           <img src="../../../assets/imgs/logo/logo.png" alt="">
         </div>
         <div class="rightTitle" v-if="false">
           <h2>数据库原理及应用</h2>
           <h3>精品课程共享</h3>
         </div>
       </div>
      </div>
    </div>
    <Menus></Menus>
  </div>
</template>

<script>
  import Menus from '../Menus/menus'
  export default {
    name: '',
    components: {Menus},
  }
</script>

<style lang="less">
  .userNav{
    .navOuter{
      position: relative;
      overflow: hidden;
      background-color: #A9DFFF;
      background-image: url("./header-background.jpg");
      background-size: 100% 100%;
      background-repeat: no-repeat;
      z-index: -1;
      .nacInner{
        height: 150px;
        line-height: 150px;
        .leftImg{
          float: left;
        }
        .rightTitle{
          display: inline-block;
          width: calc(100% - 280px);
          h2{
            font-family: 'MicrosoftYaHei';
          }
        }
      }
    }
  }
</style>



