<template>
  <div class="myUserCourse">
    <div class="header">
      <Nav></Nav>
    </div>
    <div class="body">
      <div class="container">
        <router-view></router-view>
      </div>
    </div>
    <div class="footer">
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
  import Nav from '../../../components/User/Nav/nav'
  import Footer from '../../../components/User/Footer/footer'
  export default {
    name: '',
    components: {
      Nav,
      Footer
    },
  }
</script>

<style lang="less">
  .myUserCourse{
    position: relative;
    .body{
      min-height: calc(100vh - 240px);
    }
  }
  @media screen and (max-width: 767px) {
    .myUserCourse{
      .body{
        .container{
          margin: 0!important;
          max-width: 100%;
        }
      }
    }
  }
</style>



